<template>
  <main>
    {{ $t("system.loginLoading") }}
  </main>
</template>

<script>
import axios from "axios";

const getParameterByName = function (name, url = window.location.href) {
  name = name.replace(/[[\]]/g, "\\$&");
  var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return "";
  return decodeURIComponent(results[2].replace(/\+/g, " "));
};

export default {
  name: "LoginView",
  setup() {
    const code = getParameterByName("code");
    const state = getParameterByName("state");
    console.log(code);
    if (code && state) {
      if (state === window.localStorage.getItem("state")) {
        let params = {
          grant_type: "authorization_code",
          client_id: "97e67934-92a6-4f26-a2f6-5cecc2f380d5",
          redirect_uri: window.location.origin + "/#/login",
          code_verifier: window.localStorage.getItem("verifier"),
          code: code,
        };
        axios
          .post("https://para-member-dev.para-daily.com/oauth/token", params)
          .then((resp) => {
            window.opener.postMessage(resp.data);
            // localStorage.removeItem("state");
            // localStorage.removeItem("verifier");
            window.close();
          })
          .catch((e) => {
            console.log(e);
          });
      }
    }
    localStorage.removeItem("state");
    localStorage.removeItem("verifier");
  },
};
</script>
<style scoped>
main {
  font-weight: 900;
  font-size: 1em;
}
</style>
