<template>
  <main>
    <div class="container">
      <div class="row">
        <h1>{{ this.oContent.title }}</h1>
        <template v-for="(part, index) in this.oContent.parts" :key="index">
          <h4 v-if="part.title">{{ part.title }}</h4>
          <div class="contentHTML" v-html="part.contentHTML"></div>
          <hr />
        </template>
      </div>
    </div>
  </main>
</template>

<script>
import termsOfService from "@/assets/terms-of-service.json";
import privacyPolicy from "@/assets/privacy-policy.json";
import pointsTermsOfService from "@/assets/points-terms-of-service.json";

export default {
  data() {
    return {
      oContent: {},
    };
  },
  mounted() {
    if (this.$route.path == "/terms-of-service") {
      this.oContent = termsOfService;
    } else if (this.$route.path == "/privacy-policy") {
      this.oContent = privacyPolicy;
    } else if (this.$route.path == "/points-terms-of-service") {
      this.oContent = pointsTermsOfService;
    }
    // console.log(this.$router);
  },
};
</script>

<style scoped lang="scss">
main {
  background-color: white;
  .row {
    padding-top: 2em;
    padding-bottom: 2em;
    justify-content: start;
    h1 {
      margin-top: 1em;
      margin-bottom: 1em;
      width: unset;
    }
    h4 {
      margin-bottom: 0.5em;
      text-align: left;
    }
    .contentHTML {
      text-align: left;
    }
  }
}
</style>
