<template>
  <header class="headerBar" v-if="!checkLoginLoading()">
    <div class="container">
      <div class="row">
        <nav class="navbar navbar-expand-lg" data-mdb-right="true">
          <div class="container">
            <a class="navbar-brand" href="#">
              <img alt="" src="@/assets/Logo.svg" height="45" />
            </a>
            <button
              id="moblie_menu_btn"
              class="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarNav"
              aria-controls="navbarNav"
              aria-expanded="false"
              aria-haspopup="true"
              aria-label="Toggle navigation"
              v-if="getCookies()"
            >
              <span class="navbar-toggler-icon navbar-dark"></span>
            </button>
            <div class="collapse navbar-collapse" v-if="getCookies()">
              <ul class="navbar-nav">
                <li class="nav-item">
                  <router-link to="/" active-class="active">{{
                    $t("header.home")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <router-link to="/Games1" active-class="active">{{
                    $t("header.featuredGames")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <router-link to="/Games2" active-class="active">{{
                    $t("header.freeGames")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <router-link to="/Contact" active-class="active">{{
                    $t("header.contactUs")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <router-link to="/News" active-class="active">{{
                    $t("header.newsList")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <a href="https://news.murax2.com/" target="_blank">
                    {{ $t("header.adultInfo") }}
                  </a>
                  <div class="selected"></div>
                </li>
                <li class="nav-item btns_li" v-if="!login">
                  <button type="button" class="btn btn-pink" @click="loginPC">
                    {{ $t("header.login") }}
                  </button>
                  <button type="button" class="btn btn-pink">
                    {{ $t("header.signup") }}
                  </button>
                </li>

                <li class="nav-item btns_li login_btns" v-if="login">
                  <button type="button" class="btn btn-pink">
                    {{ $t("header.storedPoint") }}
                  </button>
                  <div class="account_btn" data-bs-toggle="dropdown">
                    <i class="bi bi-person-circle"></i>
                    <i class="bi bi-caret-down-fill"></i>
                  </div>
                  <ul class="dropdown-menu dropdown-menu-end">
                    <li class="account">
                      {{
                        this.userData.nickname +
                        "(" +
                        this.userData.username +
                        ")"
                      }}
                      <i class="bi bi-caret-up-fill"></i>
                    </li>
                    <hr />
                    <li class="point">
                      {{ this.userData.remaining_point + " P" }}
                      <i class="bi bi-plus-square-fill"></i>
                    </li>
                    <li>{{ $t("header.accountManagement") }}</li>
                    <li>{{ $t("header.myGames") }}</li>
                    <hr />
                    <li @click="logoutPC">{{ $t("header.logout") }}</li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </div>
  </header>
  <div class="collapse" id="navbarNav" v-if="getCookies()">
    <ul class="navbar-nav">
      <li class="nav-item">
        <router-link to="/" active-class="active" @click="clickMenu">{{
          $t("header.home")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <router-link to="/Games1" active-class="active" @click="clickMenu">{{
          $t("header.featuredGames")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <router-link to="/Games2" active-class="active" @click="clickMenu">{{
          $t("header.freeGames")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <router-link to="/Contact" active-class="active" @click="clickMenu">{{
          $t("header.contactUs")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <router-link to="/News" active-class="active" @click="clickMenu">{{
          $t("header.newsList")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <!-- <router-link to="/AdultInfo" active-class="active" @click="clickMenu">{{
          $t("header.adultInfo")
        }}</router-link> -->
        <a href="https://news.murax2.com/" target="_blank" @click="clickMenu">
          {{ $t("header.adultInfo") }}
        </a>
        <div class="selected"></div>
      </li>
      <li class="nav-item btns_li" v-if="!login">
        <button type="button" class="btn btn-pink" @click="loginMoblie">
          {{ $t("header.login") }}
        </button>
        <button type="button" class="btn btn-pink" @click="clickMenu">
          {{ $t("header.signup") }}
        </button>
      </li>
      <div class="login_btns_moblie" v-if="login">
        <hr />
        <li class="point nav-item">
          <span>
            {{ this.userData.remaining_point + " P" }}
          </span>
          <button type="button" class="btn btn-pink">
            {{ $t("header.storedPoint") }}
          </button>
        </li>
        <hr />
        <li
          class="account nav-item"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav_account_moblie"
        >
          {{ this.userData.nickname + "(" + this.userData.username + ")" }}
          <i class="bi bi-caret-down-fill"></i>
        </li>
      </div>
    </ul>
    <ul id="navbarNav_account_moblie" class="collapse navbar-nav">
      <li class="nav-item">{{ $t("header.accountManagement") }}</li>
      <li class="nav-item">{{ $t("header.myGames") }}</li>
      <li class="nav-item" @click="logoutMoblie">{{ $t("header.logout") }}</li>
    </ul>
  </div>
</template>

<script>
import $ from "jquery";
import qs from "qs";
import crypto from "crypto-js";
import axios from "axios";
import { reactive } from "vue";
let THIS;
const createRandomString = (num) => {
  return [...Array(num)].map(() => Math.random().toString(36)[2]).join("");
};
const base64Url = (string) => {
  return string
    .toString(crypto.enc.Base64)
    .replace(/\+/g, "-")
    .replace(/\//g, "_")
    .replace(/=/g, "");
};
const tokenData = reactive({
  token_type: "",
  expires_in: "",
  access_token: "",
  refresh_token: "",
});
export default {
  name: "HeaderBar",
  data() {
    return {
      login: false, //false
      userData: {
        username: "shawn",
        email: "shawn@para-daily.com",
        nickname: "Meow",
        avatar_url:
          "https://para-member-dev.s3.amazonaws.com/users/avatar/images/default.png",
        remaining_point: 123456,
      },
    };
  },
  setup() {
    window.addEventListener("message", (e) => {
      if (
        e.origin !== window.location.origin ||
        !Object.keys(e.data).includes("access_token")
      ) {
        return;
      }
      const { token_type, expires_in, access_token, refresh_token } = e.data;
      tokenData.token_type = token_type;
      tokenData.expires_in = expires_in;
      tokenData.access_token = access_token;
      tokenData.refresh_token = refresh_token;
      THIS.setCookieToken();
      THIS.getUserData();
    });
  },
  mounted() {
    THIS = this;
    if (this.login == false) {
      // if (window.localStorage.getItem("MURA_access_token"))
      //   this.getUserData(window.localStorage.getItem("MURA_access_token"));
      if (this.$cookies.get("MURA_access_token"))
        this.getUserData(this.$cookies.get("MURA_access_token"));
      else if (this.$cookies.get("MURA_refresh_token")) {
        this.refreshToken();
      }
    }
  },
  methods: {
    getCookies() {
      return this.$cookies.get("cover18_check_auth");
    },
    checkLoginLoading() {
      return (
        window.localStorage.getItem("state") &&
        window.localStorage.getItem("verifier")
      );
    },
    clickMenu() {
      $("#moblie_menu_btn").click();
    },
    loginMoblie() {
      this.loginPC();
      this.clickMenu();
    },
    loginPC() {
      const urlData = reactive({
        response_type: "code",
        client_id: "97e67934-92a6-4f26-a2f6-5cecc2f380d5",
        redirect_uri: window.location.origin + "/#/login",
        scope: "game-site",
        prefix: "game-site",
        state: "",
        code_challenge: "",
        code_challenge_method: "S256",
      });
      urlData.state = createRandomString(40);
      const verifier = createRandomString(128);
      urlData.code_challenge = base64Url(crypto.SHA256(verifier));
      window.localStorage.setItem("state", urlData.state);
      window.localStorage.setItem("verifier", verifier);
      console.log(urlData);
      const url =
        "https://para-member-dev.para-daily.com/oauth/authorize?" +
        qs.stringify(urlData);
      open(url, "LoginWindow", "width=700,height=700");
    },
    logoutMoblie() {
      this.logoutPC();
      $(".login_btns_moblie").find(".account").click();
      this.clickMenu();
    },
    logoutPC() {
      const options = {
        method: "POST",
        headers: {
          Authorization: "Bearer " + this.$cookies.get("MURA_access_token"),
        },
        url: "https://para-member-dev.para-daily.com/api/logout",
      };
      axios
        .request(options)
        .then(function () {
          // console.log(res);
          THIS.$cookies.remove("MURA_access_token");
          THIS.$cookies.remove("MURA_refresh_token");
          THIS.login = false;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    getUserData(accessToken) {
      const options = {
        method: "GET",
        headers: {
          Authorization: "Bearer " + (accessToken || tokenData.access_token),
        },
        url: "https://para-member-dev.para-daily.com/api/game-site/user/get",
      };
      axios
        .request(options)
        .then(function (res) {
          // console.log(res);
          THIS.userData = res.data;
          THIS.login = true;
        })
        .catch(function (err) {
          // THIS.refreshToken();
          console.log(err);
        });
    },
    refreshToken() {
      let params = {
        grant_type: "refresh_token",
        client_id: "97e67934-92a6-4f26-a2f6-5cecc2f380d5",
        refresh_token: this.$cookies.get("MURA_refresh_token"),
        scope: "game-site",
        prefix: "game-site",
      };
      axios
        .post("https://para-member-dev.para-daily.com/oauth/token", params)
        .then((resp) => {
          const { expires_in, access_token, refresh_token } = resp.data;
          tokenData.expires_in = expires_in;
          tokenData.access_token = access_token;
          tokenData.refresh_token = refresh_token;
          THIS.setCookieToken();
          THIS.getUserData(access_token);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    setCookieToken: function () {
      THIS.$cookies.set(
        "MURA_access_token",
        tokenData.access_token,
        tokenData.expires_in - 60 * 60 * 24
      );
      THIS.$cookies.set(
        "MURA_refresh_token",
        tokenData.refresh_token,
        tokenData.expires_in * 2 - 60 * 60 * 24
      );
    },
  },
};
</script>

<style scoped lang="scss">
.btn-pink {
  --bs-btn-color: #ff3885;
  --bs-btn-bg: #ffffff;
  --bs-btn-border-color: #ffffff;
  --bs-btn-hover-color: #ff3885;
  --bs-btn-hover-bg: #ffdae7;
  --bs-btn-hover-border-color: #ffdae7;
  --bs-btn-active-color: #ff3885;
  --bs-btn-active-bg: #ffdae7;
  --bs-btn-active-border-color: #dfdbdb;
  --bs-btn-active-shadow: inset 0 3px 5px #ffffff;
  --bs-btn-disabled-color: #ff3885;
  --bs-btn-disabled-bg: #ff5187;
  --bs-btn-disabled-border-color: #ff5187;
}
.headerBar {
  background-color: #ff3885;
  height: 100px;
  display: flex;
  align-items: center;
}
.navbar-toggler {
  margin-left: 0.5em;
  margin-right: 0.5em;
  border-color: #ffffff;
}

.nav-item {
  a {
    font-weight: bold;
    font-size: 1.3rem;
    color: #ffffff;
    text-decoration: none;
    margin: 0 13px;
  }
  button {
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    font-weight: bold;
    font-size: 1.1rem;
    margin: 0 13px;
  }
  .selected {
    height: 5px;
    margin: 0 15px;
    background-color: #ffa0c2;
    opacity: 0;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
  }
  > .active ~ .selected {
    opacity: 0.9;
  }
  &:hover .selected {
    opacity: 0.9;
  }
}
.navbar-collapse {
  position: relative;
  .btns_li {
    position: absolute;
    right: 0;
  }
  .login_btns {
    display: flex;
    align-items: center;
    .account_btn {
      display: flex;
      align-items: center;
      &:hover .bi-person-circle {
        color: #ffdae7;
      }
      &:hover .bi-caret-down-fill {
        color: #ffdae7;
      }
      .bi-person-circle {
        font-size: 25px;
        color: #ffffff;
        margin-left: 2px;
        margin-right: 2px;
        cursor: pointer;
      }
      .bi-caret-down-fill {
        color: #ffffff;
        cursor: pointer;
      }
    }
    .dropdown-menu {
      width: 95%;
      border-radius: 3px;
      padding: unset;
      color: #ff3885;
      font-weight: 900;
      .account,
      .point {
        display: flex;
        justify-content: space-between;
      }
      .point {
        color: #fa79fe;
      }
      li {
        padding-left: 0.8em;
        padding-right: 0.8em;
        padding-top: 0.5em;
        padding-bottom: 0.5em;
        cursor: pointer;
        &:hover {
          background-color: #ffdae7;
        }
      }
      hr {
        margin: unset;
      }
    }
  }
}

#navbarNav {
  position: absolute;
  z-index: 999;
  width: 100%;
  background-color: #ff3885;
  .navbar-nav {
    opacity: 0;
  }
  .nav-item {
    margin-top: 5px;
    margin-bottom: 5px;
    .selected {
      margin: 0 auto;
      width: 4em;
    }
  }
  .btns_li {
    flex-direction: row !important;
    justify-content: center;
  }
  .login_btns_moblie {
    font-weight: bold;
    color: #ffffff;
    padding-top: 0.5em;
    hr {
      margin: unset;
      border-bottom: 1px solid #ffffff;
    }
    .point {
      padding-top: 1em;
      padding-bottom: 1em;
      button {
        margin-left: 1.5em;
      }
    }
    .account {
      padding-top: 0.5em;
      padding-bottom: 0.5em;
      cursor: pointer;
    }
  }
}
#navbarNav_account_moblie {
  background-color: #f7f6ef;
  font-weight: bold;
  li {
    padding-top: 0.1em;
    padding-bottom: 0.1em;
    cursor: pointer;
  }
}
.show .navbar-nav {
  opacity: 1 !important;
}

@media (max-width: 1400px) {
  .navbar-brand img {
    height: 40px;
  }
  .nav-item {
    a {
      font-size: 1.2rem;
      margin: 0 10px;
    }
    button {
      font-size: 1rem;
      margin: 0 10px;
    }
    .selected {
      margin: 0 12px;
    }
  }
}
@media (max-width: 1200px) {
  .navbar-brand img {
    height: 35px;
  }
  .nav-item {
    a {
      font-size: 1.1rem;
      margin: 0 5px;
    }
    button {
      font-size: 1rem;
      margin: 0 5px;
    }
    .selected {
      margin: 0 7px;
    }
  }
}
</style>
