<template>
  <footer class="footerBar" v-if="!checkLoginLoading()">
    <div class="container">
      <div class="row">
        <div class="logoDiv">
          <a class="navbar-brand" href="#">
            <img alt="" src="@/assets/Logo.svg" height="45" />
          </a>
        </div>
      </div>
      <div class="row">
        <div class="socialDiv">
          <a href="https://twitter.com/muramura_tw" target="_blank">
            <img alt="" src="@/assets/Twitter.png" />
          </a>
          <!-- <a href="#">
            <img alt="" src="@/assets/Youtube.png" />
          </a> -->
          <a href="https://www.facebook.com/ParaDaily" target="_blank">
            <img alt="" src="@/assets/Facebook.png" />
          </a>
        </div>
      </div>
      <div class="row footer_content">
        <p class="">
          {{ $t("footer.content") }}
        </p>
      </div>
      <div class="row footer_bottom_content row-cols-auto">
        <router-link class="col" to="/terms-of-service">{{
          $t("footer.termsOfUse")
        }}</router-link>
        <router-link class="col" to="/privacy-policy">{{
          $t("footer.privacyPolicy")
        }}</router-link>
        <router-link class="col" to="/points-terms-of-service">{{
          $t("footer.pointTermsOfService")
        }}</router-link>
        <a class="col" href="mailto:official@para-daily.com">{{
          $t("footer.businessCooperation")
        }}</a>
      </div>
      <div class="row langSelect_bar">
        <p class="copyRightWord">{{ $t("footer.copyRight") }}</p>
        <select
          class="langSelect form-select"
          v-model="locale"
          @change="changeLang"
        >
          <option
            v-for="item in localeOptions"
            :key="`locale-${item.lang}`"
            :value="item.lang"
          >
            🌐 {{ item.name }}
          </option>
        </select>
      </div>
      <div class="row langSelect_bar_moblie">
        <div>
          <select
            class="langSelect form-select"
            v-model="locale"
            @change="changeLang"
          >
            <option
              v-for="item in localeOptions"
              :key="`locale-${item.lang}`"
              :value="item.lang"
            >
              🌐 {{ item.name }}
            </option>
          </select>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
import { useI18n } from "vue-i18n";
export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  setup() {
    const { locale } = useI18n();
    return {
      locale,
      localeOptions: [
        // {
        //   lang: "en",
        //   name: "English",
        // },
        {
          lang: "zh-Hant-TW",
          name: "繁體中文",
        },
        {
          lang: "zh-Hans-CN",
          name: "简体中文",
        },
      ],
    };
  },
  methods: {
    changeLang() {
      console.log("change");
      this.$cookies.set("MURA-lang", this.locale);
      location.reload();
    },
    checkLoginLoading() {
      return (
        window.localStorage.getItem("state") &&
        window.localStorage.getItem("verifier")
      );
    },
  },
};
</script>

<style scoped lang="scss">
.footerBar {
  background-color: #ff3885;
  display: flex;
  align-items: center;
  padding: 20px;
  .logoDiv {
    margin-top: 2vh;
    margin-bottom: 2vh;
  }
  .socialDiv {
    margin: 20px 0;
    a {
      margin: 0 5px;
    }
    img {
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      &:hover {
        opacity: 0.7;
        transform: scale(0.95);
      }
      &:active {
        opacity: 1;
        transform: scale(1.1);
      }
    }
  }
  .footer_content {
    flex-direction: column;
    align-items: center;
    p {
      color: #dcdddd;
      font-weight: bold;
      max-width: 550px;
      text-align: left;
      margin: 2vh 0;
    }
  }
  .footer_bottom_content {
    margin-top: 2vh;
    margin-bottom: 2vh;
    justify-content: center;
    a {
      color: #dcdddd;
      text-decoration: none;
      font-weight: bold;
      font-size: 1.3em;
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .copyRightWord {
    margin-top: 1vh;
    color: #dcdddd;
    font-weight: bold;
  }
  .langSelect_bar_moblie {
    position: relative;
    display: none;
    height: 40px;
  }
  .langSelect_bar {
    position: relative;
  }
  .langSelect {
    position: absolute;
    width: auto;
    right: 1em;
    bottom: 1em;
  }
}

@media (max-width: 576px) {
  .footer_bottom_content {
    a {
      font-size: 1.2em !important;
    }
  }
  .copyRightWord,
  .footer_content {
    font-size: 0.8em !important;
  }
}
@media (max-width: 992px) {
  .langSelect_bar_moblie {
    display: flex !important;
    justify-content: flex-end;
    div {
      width: unset;
    }
    .langSelect {
      position: unset;
      right: 0;
      bottom: 0;
    }
  }
  .langSelect_bar .langSelect {
    display: none;
  }
}
</style>
